import api from '~api'
import config from '~api-config'

let mdTable = (params) => api.post('/base/store/list.action',config.api,params);//
let addMd= (params) => api.post('/base/store/create.action',config.api,params);//
let editMd= (params) => api.post('/base/store/edit.action',config.api,params);//
let updateMd= (params) => api.post('/base/store/update.action',config.api,params);//()
let deleteMd= (params) => api.post('/base/store/delete.action',config.api,params);//
let sytList= (params) => api.post('/base/cashier/list.action',config.api,params);//
let updateSyt= (params) => api.post('/base/cashier/update.action',config.api,params);//
let addSyt= (params) => api.post('/base/cashier/create.action',config.api,params);//
let deleteSyt= (params) => api.post('/base/cashier/delete.action',config.api,params);//
let wldwxz= () => api.post('/base/company/list.action',config.api);//
let editwldw= (params) => api.post('/base/company/edit.action',config.api,params);//
let ckList= (params) => api.post('/base/depot/list.action',config.api,params);//
let addCk= (params) => api.post('/base/depot/create.action',config.api,params);//

export default {
    mdTable,
    addMd,
    editMd,
    updateMd,
    deleteMd,
    sytList,
    updateSyt,
    addSyt,
    deleteSyt,
    wldwxz,
    editwldw,
    ckList,
    addCk
}



